<template>
  <div class="container">
    <div v-if="loading">
      loading...
    </div>
    <div v-else>
      <!-- 文章内容 -->
      <mu-paper class="content">
        <div class="title">
          {{topic.title}}
        </div>
        <div class="topic-info">
          <p class="time">发布 {{createdDate}}前</p>
          <p class="name">作者 {{topic.author.loginname}}</p>
          <!-- <img :src="topic.author.avatar_url"> -->
        </div>
        <mu-divider />
        <div>
          <div v-html="topic.content">
        </div>
      </mu-paper>
      <!-- 回复 -->
      <mu-paper class="reply">
        <div>共{{topic.reply_count}}条回复：</div>
        <div v-for="reply in topic.replies">
          <topic-reply
            :author="reply.author"
            :content="reply.content"
            :replyTime="reply.create_at"
          />
          <mu-divider/>
        </div>
      </mu-paper>
    </div>
  </div>
</template>

<script>
  import cnode from '../utils/api';
  import TopicReply from './TopicReply';
  import { toRelativeTime } from '../utils/utils';

  export default {
    name: 'topic',
    data() {
      return {
        topic: null,
        loading: true,
      };
    },
    created() {
      this.getTopic().then(data => {
        console.log(data)
        this.loading = false;
        this.topic = data;
      })
    },
    methods: {
      getTopic() {
        let topicId = this.$route.params.topicId;
        return cnode('topic_details',{
          pathParams: [topicId]
        });
      },
    },
    computed: {
      createdDate() {
        let mesc = new Date() - new Date(this.topic.create_at);
        return toRelativeTime(mesc);
      },
    },
    components: { TopicReply }
  };
</script>

<style scoped>
  .container {
    margin: ;
  }
  .content {
    padding: 10px;
    word-break: break-all;
  }
  .reply {
    margin-top: 10px;
    padding: 10px;
    top: 5px;
  }
  .title {
    font-size: 19px;
  }
  .topic-info {
    color: #616161;
    font-size: 14px;
    width: 100%;
    float: left;
    display: block;
  }
  .name {
    margin-left: 5px;
    float: left;
  }
  .time {
    float: left;
  }
</style>



